          bizDetail: '家用车，新车，单三者，同保',
          insuranceType: '0528',
          finalPayRate: '56.6',
          auto: '0.56',
          historyAuto: '1.00',
          premiumPercent: '11.8',
          serviceCost: '0.8',
          manageCost: '1.2',
